import {
    GETUSERINFO,
    LOGINSTATE,
    RENAMESTATE
} from '../constants/login'

const initState = {
    userInfo: {
        isLogin: false,
        info: {
            nickName: null,
            passportId: null,
            iconUrl: null,
            realAuth: null,
            faceAuth: null,
            intro: null,
            token: null,
            phone: null
        }
    },
    userState: {
        loginType: 'account', // account,sms,register,retrievePassword,wechat,bind
        loginShow: false,
        renameShow: false // 
    }
}

export default (state = initState, action) => {
    switch (action.type) {
        case GETUSERINFO:
            return { ...state, userInfo: action.data }
        case LOGINSTATE:
            return { ...state, userState: action.data }
        case RENAMESTATE:
            return { ...state, userState: action.data }
        default:
            return state
    }
}
