import { promisify } from 'util'
import express from 'express'
import request from 'request'

import sign from './signture/sign'
import clientDB from './signture/client'

const router = express.Router()
const client = clientDB()

const getAsync = promisify(client.get).bind(client) // 
const setAsync = promisify(client.set).bind(client) // 
const hmsetAsync = promisify(client.hmset).bind(client) // json
const hgetallAsync = promisify(client.hgetall).bind(client) // json

