import i18next from 'i18next'
import i18nMiddleware, { LanguageDetector } from 'i18next-express-middleware'
import Backend from 'i18next-node-fs-backend'
import resources from '../../_multiappsharing/public/i18n'

export default (app) => {
    i18next
        .use(Backend)
        .use(LanguageDetector)
        .init({
            react: {
                useSuspense: false
            },
            preload: ['zh-CN', 'zh-TW'],
            resources,
            lng: 'en', // i18n.changeLanguagebrowser/i18n.js
            fallbackLng: ['zh-CN', 'zh-TW'],
            lowerCaseLng: true,
            // keySeparator: false, // one.two
            interpolation: {
                escapeValue: false
            }
        }, (err, t) => {
            if (err) throw Error(err)
        })
    app.use(
        i18nMiddleware.handle(i18next, {
            ignoreRoutes: ['/foo'], // or function(req, res, options, i18next) { /* return true to ignore */ }
            removeLngFromUrl: false
        })
    )
}
