import { HEADERCHANNELS } from '../constants/header'
import { mixUrl } from '../../public'

const navData = [
    { channelName: '', channelId: '0', url: mixUrl.m() },
    { channelName: '', channelId: '100', url: mixUrl.m(`/flash`) },
    { channelName: '', channelId: '200', url: mixUrl.m(`/video`) },
    { channelName: '', channelId: '300', url: mixUrl.m(`/huodong`) },
    { channelName: '', channelId: '500', url: mixUrl.m('/feature') }
]

const initState = {
    channelsList: []
}

export default (state = initState, action) => {
    switch (action.type) {
        case HEADERCHANNELS:
            return { ...state, channelsList: navData.concat(action.data) }
        default:
            return state
    }
}
