import path from 'path'
import fs from 'fs'

import { isObject } from 'multiPublic/index'

const winston = require('winston')
const { createLogger, format, transports } = winston
require('winston-daily-rotate-file')

// logs
const logDirectory = path.resolve('./', 'logs')
fs.existsSync(logDirectory) || fs.mkdirSync(logDirectory)

// 
const transportFile = (obj) => new (winston.transports.DailyRotateFile)(Object.assign({
    datePattern: 'YYYY-MM-DD-HH',
    // zippedArchive: true,
    maxSize: '20m',
    maxFiles: '1d'
}, obj))

// 
const formatPrintf = (info) => {
    try {
        let { timestamp, level, message } = info
        let obj = {
            level: level,
            timestamp: timestamp,
            message: message
        }

        if (info.meta) { // express-winstonmeta
            const meta = info.meta
            const req = meta.req
            obj.url = `${req.method} ${req.headers.host + req.originalUrl}`
            req.query && JSON.stringify(req.query) !== '{}' && (obj.query = JSON.stringify(req.query))
            req.body && JSON.stringify(req.body) !== '{}' && (obj.body = JSON.stringify(req.body))

            meta.error && Object.keys(meta.error).map(function (key, index) { // errpath errmessage
                const val = meta.error[key]
                obj[`err_${key}`] = isObject(val) ? (JSON.stringify(val) !== '{}' ? JSON.stringify(val) : '') : val
            })
            meta.stack && (obj.stack = meta.stack.replace(/[\r\n]/g, ''))

            obj.cookies = `${req.headers.cookie}`
            obj.userAgent = req.headers['user-agent']

            meta.res && Object.keys(meta.res).map(function (key, index) { // resstatusCode
                const val = meta.res[key]
                obj[`res_${key}`] = isObject(val) ? (JSON.stringify(val) !== '{}' ? JSON.stringify(val) : '') : val
            })
            meta.responseTime && (obj.responseTime = meta.responseTime)

            obj.meta = JSON.stringify(meta)
        } else {
            Object.keys(info).map(function (key, index) {
                if (key !== 'level' && key !== 'timestamp' && key !== 'message') {
                    if (key === 'stack' || key === 'componentStack') {
                        obj[key] = info[key].replace(/[\r\n]/g, '')
                    } else {
                        const val = info[key]
                        obj[key] = isObject(val) ? (JSON.stringify(val) !== '{}' ? JSON.stringify(val) : '') : val
                    }
                }
            })
        }

        return JSON.stringify(obj)
    } catch (err) {
        return JSON.stringify({
            level: 'error',
            message: 'winston printf'
        })
    }
}

const transportLevelFile = (level) => transportFile({
    level: level,
    format: format.combine(
        format.timestamp({ format: 'YYYY-MM-DD HH:mm:ss' }),
        format.printf(formatPrintf)
    ),
    filename: path.resolve(logDirectory, `${level}-%DATE%.log`)
})

const logger = createLogger({
    levels: {
        error: 0,
        warn: 1,
        info: 2,
        http: 3,
        verbose: 4,
        debug: 5,
        silly: 6
    },
    handleExceptions: true,
    json: true,
    maxsize: 5242880, // 5MB
    maxFiles: 5
})
logger.configure({
    level: 'verbose'
})
logger.configure({
    level: 'debug'
})
logger.configure({
    level: 'silly',
    transports: [
        transportFile({ // silly
            format: format.combine(
                format.timestamp({ format: 'YYYY-MM-DD HH:mm:ss' }),
                format.printf(formatPrintf)
            ),
            filename: path.resolve(logDirectory, 'info-%DATE%.log')
        }),
        // transportLevelFile('http'), // level: httphttp+info+wran+error; warnwran+error
        // transportLevelFile('error'), // errorerror
        transportLevelFile('warn'),
        new transports.Console({
            format: format.combine(
                format.colorize(),
                format.timestamp({ format: 'YYYY-MM-DD HH:mm:ss' }),
                format.printf(info => {
                    const { timestamp, level, message } = info
                    return `${timestamp} [${level}]: ${message}`
                })
            )
        })
    ]
})

/** @desc (error, warn, info, http, verbose, debug, silly)
 * RFC5424:https://tools.ietf.org/html/rfc5424
 * error:0 , ejs, middleware
 * warn:1 cpu, , code!==1
 * info:2 info,errorwarninfodebug10%
 * http:3 
 * verbose:4 
 * debug:5 
 * silly:6 
 * */

